.contact-container{
    width: 100%;
    height: 86vh;
  
}
.contact-content{
    width: 100%;height: 100%;
    background-image: url("./../Image/1620133415_977881_1619440501_428233_banner-driver-2.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    position: relative;

}
 
.contact-login{
    position: absolute;
    top: 50px;right: 250px;
    width: 350px;border: 1px solid white;
    height: 500px;
    background-color: white;
    border-radius: 5px;
}
.contact-login input{
    padding: 0px 0px 5px 15px;
    width: 250px;border-radius: 5px;border: 2px solid grey;
    }
@media screen and (max-width:1500px) {
    .contact-login{
        position: absolute;
        top: 30px;right: 100px;
        height: 400px;width: 350px;
        font-size: 12px;
    }
     
    
}
@media screen and (max-width:950px) {
    .contact-login{
        position: absolute;
        top: 40px;right: 50px;
        height: 400px;width: 350px;
    }
}
@media screen and (max-width:750px) {
    .contact-login{
        position: absolute;
        top: 40px;right: 30px;
        height: 400px;width: 300px;
    }
}
@media screen and (max-width:600px) {
    .contact-login{
        /* position: absolute;
        top: 40px;right: 120px; */
        height: 400px;width: 300px;
        border: 1px solid white;
        box-shadow: 5px 5px 1px white;
        /* transform: translate(-80px); */
      
        
    }
    .contact-content{
        background-image: none;
        background-color: grey;

       
    }
}
@media screen and (max-width:500px) {
    .contact-login{
        text-align: center;
        top: 40px;right: 30px;
        height: 400px;width: 300px;
    }
    .contact-content{
        text-align: center;
    }
      
}