*{
    margin: 0%;padding: 0;box-sizing: border-box;
 }
 .navbar{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.2rem;
    font-family: 'Heebo', sans-serif;
    background-color: #F15412;
    height: 80px;
    position:sticky;
    top: 0;
    z-index: 2;
 }
 .navbar-container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 80px;
    width: 100%;
    
 }
 .nav-logo{
    width: 150px;
    height: 80px;
    margin-bottom: 15px;
 }
 .nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
     
 }
 .nav-icon{
    display: none;
 }
 .fa-multiply{
    transform: rotate(180deg);
    transition: all 0.5s ease-in-out;
    color: white;
    font-size: 25px;
 }
 .fa-bars{
    transform: rotate(360deg);
    transition: all 0.5s ease-in-out;
    color: white;
    font-size: 25px;
 }
 .nav-item{
    margin-right: 95px;
    border-bottom: 2px solid transparent;
   
  
 }
 .nav-item a{
    color: white;
    text-decoration: none;
    border-bottom: 3px solid transparent;
    height: 100%;
 }

 .nav-item::after{
    content: " ";
    display: block;
    width: 0;
    height: 3px;
    background-color:  transparent;
    transition: all 0.5s ease;
 }
 .nav-item:hover::after{
     background-color:yellow;
    width: 100%;

 }
 @media screen and (max-width:850px) {
   .nav-icon{
    display: block;
     padding: 20px ;
   } 
   .nav-menu{
    flex-direction: column;
    /* border: 2px solid grey; */
    position: absolute;
    top: 80px;
    width: 100%;
    background-color: #F15412;
    height: 150px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease-in-out;
   }
   .nav-menu.active{
    left: -0%;
    opacity: 1;
    transition: all 0.5s ease-in-out;
    z-index: 2;
   }
 }
