.home-header-img{
    background-color: #F5EDDC;
    animation: pizza-image 8s infinite ease-in-out;
     
  }
  .home-header-img img{
    border-radius: 50%;
    width: 80%;height: 90%;
  }
  .map-btn{
   background-color: #f155127c;
   /* background-color: rgba(128, 128, 128, 0.689); */
   /* color: white; */
   color: black;
   font-weight: 800;
   width: 100%;height: 60%;opacity: 0;
   transition: all 0.5s ease-in-out;
  }
  #section3-home-image:hover .map-btn{
   opacity: 1;transition: all 0.5s ease-in-out;
  }
  
  @keyframes pizza-image {
     0%{ border-radius: 49% 51% 48% 52% / 86% 64% 36% 34% ;}
     50%{border-radius: 49% 51% 48% 52% / 70% 40% 60% 30%  ;}
     80%{
         border-radius: 49% 51% 48% 52% / 80% 79% 21% 20% ;
     }
     100%{border-radius: 49% 51% 48% 52% / 26% 18% 82% 74% ;}
  }
  .home-2nd-section{
     width: 100%;
     height: 120px;
     background-color: #F15412;
     margin-top: 10px;
     padding-top: 15px;
     display: flex;
     flex-direction: column;
     align-items: center;
     justify-content: center;
     color: white;
     font-family: 'Heebo', sans-serif;
     font-size: 20px;
  }
 #section3-home-image img{
  width: 100%;
  height: 340px;
  
 }