.about-container{
    display: flex;
    width: 100%;
    height: 90vh;
    justify-content: space-between;
    font-family: 'Heebo', sans-serif;
    /* font-size: 25px; */
    
 }
 .about-image{
    /* border-radius: 50%; */
   display: flex;
   align-items: center;
   justify-content: center;
  

    width: 60%;
    background-color: #F5EDDC;
    animation: burger-image 10s infinite ease-in-out;
 }
 .about-flex1{
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-wrap: nowrap;
 }
 .about-heading1 h1{
    font-size: 60px;
 }
 .about-image img{
    width: 80%;
    height: 90%;
    border-radius: 50%;
   

 }
 @keyframes burger-image {
    0%{ border-radius: 49% 51% 48% 52% / 66% 64% 36% 34% ;}
    50%{border-radius: 49% 51% 48% 52% / 70% 40% 60% 30%  ;}
    80%{
        border-radius: 49% 51% 48% 52% / 80% 79% 21% 20% ;
    }
    100%{border-radius: 49% 51% 48% 52% / 26% 18% 82% 74% ;}
 }
.about-txt{
    width: 100%;
    height: 100px;
    background-color: #F15412;
    color: white;
    font-family: 'Heebo', sans-serif;
  text-transform: capitalize;
  padding-top: 20px;
}

.about-section{
    width: 100%;
    height: 100px;
    background-color: #F15412;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
    text-transform: capitalize;
    font-size: 25px;
    font-family: 'Heebo', sans-serif;
}
.about-section2-img{
    width: 100%;
    height: 340px;
}
.about-section3-img{
    width: 100%;
    height: 340px;
}